<script>
export default {
  data() {
    const data = {
      v: 5,
      data: [{
        id: 1,
        text: 'root',
        child: [{
          id: 2,
          text: 'level1-1',
          child: [{
            id: 4,
            text: 'level2-1'
          }, {
            id: 3,
            text: 'level2-2',
            child: [{
              id: 5,
              text: 'level3-1',
