                id: 6,
                text: 'level4-1'
              }, {
                id: 7,
                text: 'level4-2'
              }, {
                id: 8,
                text: 'level4-3'
              }]
            }]
          }]
        }]
      }]
    }
    let id = 30
    let i = 30
    while (i--) {
      data.data[0].child.push({
        id: id++,
        text: 'id' + id
      })
    }
    return data
